import React, { useState, useEffect } from 'react';
import Base from '../core/Base';
import { isAutheticated } from '../auth/helper';
import { Link } from 'react-router-dom';
import { updateCategory, getCategory } from './helper/adminapicall';


const UpdateCategory = ({ match }) => {

    const [name, setName] = useState("")
    const [error, setError] = useState(false);
    const [success, setSuccess] = useState(false);

    const { user, token } = isAutheticated()


    const goBack = () => {
        return (

            <div className="mt-5">
                <Link className="btn btn-sm btn-success mb-3" to="/admin/dashboard">
                    Admin Home
                </Link>
            </div>


        )
    }

    const preload = (categoryId) => {
        getCategory(categoryId).then(data => {
            //console.log(data);
            if (data.error) {
                setError(true)
            } else {
                setName(data.name);
            }
        });
    };


    const handleChange = event => {
        setError("");
        setName(event.target.value)
    };


    useEffect(() => {
        preload(match.params.categoryId);
    }, []);

    const onSubmit = (event) => {
        event.preventDefault();
        setError("");
        setSuccess(false)

        //backend request fireed


        console.log(`
            ${match.params.categoryId}
            ${user._id}
            ${name}
            `);
        updateCategory(match.params.categoryId, user._id, token, { name }).then(data => {

            if (data.error) {
                console.log(data.error);
                setError(true);
            } else {
                setSuccess(true);
                setName("");
            }
        });



    }



    const successMessage = () => {
        if (success) {
            return <h4 className="text-success">
                category Updated successfully
            </h4>
        }
    }


    const warningMessage = () => {
        if (error) {
            return <h4 className="text-danger">
                failed to Update category
            </h4>
        }
    }


    const myCategoryForm = () => {
        return (

            <form>
                <div className="form-group">
                    <p className="lead">Enter the Category</p>
                    <input type="text" className="form-control my-3"
                        onChange={handleChange}
                        value={name}
                        autoFocus
                        required
                        placeholder="For Ex. Summer"
                    />
                    <button onClick={onSubmit} className="btn btn-outline-info">
                        Update Category
                    </button>
                </div>


            </form>
        )
    }

    return (
        <Base title="Create a category here"
            description="add new category for new Tshirt "
            className="container bg-info p-4"
        >

            <div className="row bg-white rounded">

                <div className="col-md-8 offset-md-2">
                    {successMessage()}
                    {warningMessage()}
                    {myCategoryForm()}
                    {goBack()}
                </div>

            </div>


        </Base>
    );
}

export default UpdateCategory;
